import { h } from 'preact';
import { useEffect, useState, useContext } from 'preact/hooks';
import { route } from 'preact-router';
import { Link } from 'preact-router/match';
import { getAuth, setPersistence, signInWithEmailAndPassword, browserLocalPersistence } from '../../scripts/firebase-auth';

import { AuthContext } from '../../components/Auth';
import Error_MSG from '../../components/Error_MSG';
import style from './style.css';

const Login = () => {

  const { currentUser } = useContext(AuthContext);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [buttonLoading, setButtonLoading] = useState(false);
  const [loginError, setLoginError] = useState({
      error: false,
      msg: ""
  });
  const [activeRoute, setActiveRoute] = useState('index.html');

  useEffect(() => {
    const handleRoute = async () => {
      // Redirect to home on success.

      let queryOptions = { active: true, currentWindow: true };
      let [tab] = await chrome.tabs.query(queryOptions);

      const regex = /admin\/menus\/[0-9]+(\?.*)?$/;
      const regex1 = /store\/.*\/menus\/[0-9]+(\?.*)?$/;
      const regex2 = /admin\/menus\/?(\?.*)?$/;
      const regex3 = /store\/.*\/menus\/?(\?.*)?$/;
      //console.log(tab);

      if (regex.test(tab.url) || regex1.test(tab.url)) {
        setActiveRoute('/menu');
      } else if (regex2.test(tab.url) || regex3.test(tab.url)) {
        setActiveRoute('/menus');
      } else {
        //console.log('%cIn Else Block:', 'color:blue;background:red;padding:5px;');
      }
    };

    handleRoute();
  });

  useEffect(() => {
    //console.log('%cThis is currentUser:', 'color:#FFF;background:#000;padding:5px;', currentUser);
    if (currentUser) {
      //console.log(`[Redirecting] redirect to ${activeRoute}...`);
      route(activeRoute);
    }

  }, [currentUser]);

  useEffect(() => {
    setLoginError({ error: false, msg: "" });
  }, []);

  const handleEmailChange = event => {
    setLoginError({ error: false, msg: "" });
    setEmail(event.target.value);
  };

  const handlePasswordChange = event => {
    setLoginError({ error: false, msg: "" });
    setPassword(event.target.value);
  };

  const loginHandler = () => {
    setLoginError({ error: false, msg: "" });

    setButtonLoading(true);
    // Set persistence to none.

    const auth = getAuth();
    setPersistence(auth, browserLocalPersistence)
      .then(() => {
        // Sign in with shop and pass.
        // [START authwithemail]
        return signInWithEmailAndPassword(auth, email, password);
      })
      .then(data => {
          const { user } = data;
          //console.log('%cThis is USER:', 'color:#000;background:#FFF;padding:5px;', user);
          setButtonLoading(false);
      })
      .catch((error) => {
        // Handle Errors here.
        var errorCode = error.code;
        setButtonLoading(false);
        // [START_EXCLUDE]
        if (errorCode === 'auth/wrong-password') {
          setLoginError({ error: true, msg: "Wrong Password!" });
        } else if (errorCode === 'auth/invalid-email') {
          setLoginError({ error: true, msg: "Invalid Email!" });
        } else if (errorCode === 'auth/user-disabled') {
          setLoginError({ error: true, msg: "User Disabled!" });
        } else if (errorCode === 'auth/user-not-found') {
          setLoginError({ error: true, msg: "User Not Found!" });
        } else {
          setLoginError({ error: true, msg: "Operation failed for some reason, please try again!" });
        }
        //console.log(error);
        // [END_EXCLUDE]
      });
    // [END authwithemail]
  };

	return (
		<div class={style.body}>
      <h2>Shopify Navigation Tool</h2>
      <h5>An absolute time saver</h5>

      <h4 class={style.bottom}>Login</h4>
      <input
        type="email"
        class={style.input}
        placeholder="Email"
        autocomplete="on"
        onChange={handleEmailChange}
      />
      <input
        type="password"
        class={style.input}
        placeholder="Password" 
        onChange={handlePasswordChange}
      />
      <div class={style.actions}>
        <button
          class={`${buttonLoading ? style.loading : ""}`}
          onClick={loginHandler}
        >
          <svg
            height="16px"
            width="16px"
            fill="#000000"
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            x="0px"
            y="0px"
            viewBox="0 0 100 100"
            enable-background="new 0 0 100 100"
          >
            <title>Login</title>
            <g>
              <g>
                <path fill="none" d="M49.319,49.959c11.829,0,21.452-9.623,21.452-21.451S61.147,7.055,49.319,7.055s-21.452,9.624-21.452,21.452    S37.49,49.959,49.319,49.959z" />
                <polygon fill="none" points="58.896,54.438 39.973,54.811 49.319,69.965" />
                <path fill="none" d="M61.176,54.656L50.194,72.463c-0.187,0.303-0.518,0.488-0.875,0.488c-0.356,0-0.687-0.185-0.875-0.488    l-10.69-17.33c-11.558,5.504-19.939,18.617-21.453,33.693c-0.107,1.072,0.239,2.138,0.951,2.925    c0.697,0.77,1.652,1.194,2.691,1.194h60.111c1.039,0,1.995-0.425,2.692-1.194c0.712-0.787,1.058-1.853,0.95-2.925    C82.125,73.166,73.324,59.862,61.176,54.656z" />
                <path d="M49.319,52.014c12.962,0,23.508-10.545,23.508-23.507S62.281,5,49.319,5C36.357,5,25.812,15.546,25.812,28.508    S36.357,52.014,49.319,52.014z M49.319,7.055c11.829,0,21.452,9.624,21.452,21.452s-9.624,21.451-21.452,21.451    s-21.452-9.623-21.452-21.451S37.49,7.055,49.319,7.055z" />
                <path d="M85.743,88.621C84.066,71.925,74.469,57.762,61.278,52.48c-0.026-0.015-0.055-0.024-0.082-0.037    c-0.02-0.008-0.038-0.018-0.058-0.026c-0.003-0.001-0.007-0.001-0.01-0.002c-0.096-0.037-0.197-0.059-0.304-0.062    c-0.02-0.001-0.039-0.005-0.059-0.005c-0.008,0-0.016-0.003-0.025-0.002l-22.607,0.447c-0.006,0-0.012,0.003-0.018,0.003    c-0.02,0.001-0.04,0.007-0.06,0.009c-0.094,0.009-0.184,0.031-0.271,0.065c-0.015,0.006-0.03,0.004-0.045,0.011    C24.902,58.525,15.904,72.22,14.257,88.621c-0.165,1.648,0.371,3.292,1.472,4.508C16.821,94.336,18.318,95,19.944,95h60.111    c1.627,0,3.125-0.664,4.216-1.871C85.372,91.912,85.908,90.269,85.743,88.621z M58.896,54.438l-9.576,15.527l-9.347-15.154    L58.896,54.438z M82.747,91.75c-0.697,0.77-1.652,1.194-2.692,1.194H19.944c-1.039,0-1.995-0.425-2.691-1.194    c-0.712-0.787-1.058-1.853-0.951-2.925c1.514-15.076,9.895-28.189,21.453-33.693l10.69,17.33c0.187,0.303,0.518,0.488,0.875,0.488    c0.356,0,0.687-0.185,0.875-0.488l10.983-17.807c12.148,5.206,20.948,18.511,22.521,34.17    C83.805,89.898,83.459,90.963,82.747,91.75z" />
              </g>
            </g>
          </svg>
          <span class={style.label}>Login</span>
          <span class={style.ball_pulse}>
            <span />
            <span />
            <span />
          </span>
        </button>
      </div>
      <Error_MSG result={loginError} />
      <p class={style.footer}>
        Don't have an account? <Link class={style.route} href="/index.html">Buy</Link> or <Link class={style.route} href="/validate">Validate your license key</Link>
      </p>
		</div>
	);
}

export default Login;
